<template>
    <!-- 모달창 -->
    <div class="black_bg" v-if="modalOpen == true">
        <div class="white_bg">
            <img :src="Hotels[choice].image" class="imgS" style="width: 100%" alt="img1" />
            <h4>{{ Hotels[choice].title }}</h4>
            <p>{{ Hotels[choice].content }}</p>
            <p>{{ Hotels[choice].price }}만원</p>
            <button @click="modalOpen = false">닫기</button>
        </div>
    </div>
    <img src="./assets/logo.png" alt="logo" />
    <div class="menu">
        <a href="#" v-for="반복할이름 in nav" :key="반복할이름">{{ 반복할이름 }}</a>
    </div>
    <div v-for="(Hotel, i) in Hotels" :key="Hotel.id">
        <img :src="Hotel.image" class="imgS" alt="img1" />
        <h4
            @click="
                modalOpen = true;
                choice = i;
            "
        >
            {{ Hotel.title }}
        </h4>
        <p>{{ Hotel.price }}만원</p>
        <button @click="singoS(i)">허위매물신고</button>
        <span>신고 수:{{ singo[i] }}</span>
    </div>
</template>

<script>
import room from './assets/js/room.js';

export default {
    name: 'App',
    data() {
        return {
            choice: 0,
            Hotels: room,
            modalOpen: false,
            singo: [0, 0, 0, 0, 0],
            nav: ['호텔', '항공권', '렌터'],
            price: [100, 200, 300],
            products: ['신주쿠 워싱턴 호텔', '호텔 오리엔탈', '알마 리조트 깜라인']
        };
    },
    methods: {
        singoS: function (i) {
            this.singo[i]++;
        }
    }
};
</script>

